@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400);

body {
  background: #2980b9;
  font-family: 'Lato', sans-serif;
  padding: 2em;
}

.fullscreen{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.home {
  display: block;
}

.game {
  display: none;
}

.wrapper {
  display: flex;  
  flex-flow: row wrap;
  font-weight: bold;
  text-align: center;
}

.wrapper > * {
  padding: 30px;
  flex: 1 100%;
}

.header {
  align-self: center;
  font-size: 2.5em;
  color: #ecf0f1;
  background: #e74c3c;
}

.footer {
  align-self: center;
  font-size: 1em;
  color: #2c3e50;
}

.main {
  align-self: center;
  width: 100%;
}

.main-footer {
  align-self: center;
  width: 100%;
  opacity: 0;
  margin-top: 15px;
  margin-bottom: -50px;
}

.aside-1 {
  align-self: center;
  color: #2c3e50;
  font-size: 1.5em;
}

.aside-2 {
  align-self: center;
  color: #2c3e50;
  font-size: 1.5em;
}

.btn {
  width: 100%;
  height: 100px;
  font-size: 2em;
  color: #2c3e50;
  background: none;
  outline: none;
  cursor: pointer;
  border: none;
}

.btn:hover {
  background: #3498db;
}

.fade-out {
  animation: fadeOut ease 2s;
  -webkit-animation: fadeOut ease 2s;
  -moz-animation: fadeOut ease 2s;
  -o-animation: fadeOut ease 2s;
  -ms-animation: fadeOut ease 2s;
}

@keyframes fadeOut {
  0% {
    opacity:1;
  }
  100% {
    opacity:0;
  }
}

@media all and (min-width: 600px) {
  .aside { flex: 1 0 0; }
}

@media all and (min-width: 800px) {
  .aside-1 { order: 1; }
  .aside-2 { order: 2; }
  .footer  { order: 3; }
}
